import { Matrix } from 'mdatools/arrays';


// constant values (colors, etc)
export const LINE_COLORS = {'cal': '#2679B2', 'pv': '#D22C2F', 'target': '#2679B2', 'alt': '#2679B2'};
export const FACE_COLORS = {'cal': '#f0f8ff', 'pv': '#fff8f0', 'target': '#f0f8ff', 'alt': '#f0f8ff'};
export const LABELS_COLOR = '#a0a0a0';
export const LABELS_COLOR_SELECTED = '#909090';


export function genhash() {
    const now = new Date();
    const uniqueId = `${now.getFullYear()}${(now.getMonth() + 1)
        .toString()
        .padStart(2, '0')}${now.getDate().toString().padStart(2, '0')}${now
        .getHours()
        .toString()
        .padStart(2, '0')}${now.getMinutes().toString().padStart(2, '0')}${now
        .getSeconds()
        .toString()
        .padStart(2, '0')}${now
        .getMilliseconds()
        .toString()
        .padStart(3, '0')}`;
    return uniqueId;
}
/**
 * Parse string with data from a CSV file and create a matrix with values.
 *
 * @param {string} strData - string with all data from the CSV file.
 * @param {string} sep - symbols used for separation of values (by default ",").
 * @param {boolean} hasHeader - logical, if first row must be treated as header or not.
 * @param {boolean} hasLabels - logical, if first column must be treated as object labels or not.
 * @param {boolean} hasClasses - logical, if column next to labels must be treated as class names or not.
 *
 * @returns {Object} JSON containing matrix with parsed values, header, values and decimal separators.
 */
export function parseCSV(strData, sep, hasHeader, hasLabels, hasClassNames) {


   // function which returns true if at least one value in array can not be parsed to float
   function hasNaN(vec) {
      return vec.some(v => Number.isNaN(Number.parseFloat(v.trim())));
   }

   // set default separator symbol
   if (sep === undefined) {
      sep = ",";
   }

   // setup decimal separator
   const dec = sep === ";" ? "," : ".";

   // remove BOM symbol if any
   strData = strData.replace(/^\uFEFF/gm, "").replace(/^\u00EF?\u00BB\u00BF/gm,"");

   // remove \r symbols
   strData = strData.replace(/\r/gm, "");

   // split string with data values into array of rows
   let rows = strData.split(/\n/);

   // check if the first row contains header
   let header = [];
   let startRow = 0;
   const firstRow = rows[0].split(sep);
   if (hasHeader || (hasHeader === undefined && hasNaN(firstRow))) {
      header = firstRow;
      startRow = 1;
   }

   // filter empty rows out
   rows = rows.filter(v => v.trim().length > 1);

   // check if first column contains labels
   let labels = [];
   let startColumn = 0;
   if (hasLabels) {
      startColumn = 1;
   }

   // check if next column contains class names
   let classNames = [];
   let classes = [];
   if (hasClassNames) {
      startColumn = startColumn + 1;
   }

   // number of rows and columns in future matrix
   const nrows = rows.length - startRow;
   const ncols = firstRow.length - startColumn;


   if (nrows < 1) {
      throw Error("Data file should contain at least 1 row with values.");
   }

   if (ncols < 1) {
      throw Error("Data file should contain at least 1 column with values.");
   }

   // set rows of data as columns of matrix - it is faster
   const data = Matrix.zeros(ncols, nrows);

   // find symbol used for decimals separator
   for (let r = startRow; r < nrows + startRow; r++) {

      // parsing: trim spaces and replace non-numbers with dot (.)
      let rowValuesAll = rows[r].split(sep)

      if (hasLabels) {
         labels.push(rowValuesAll[0]);
         rowValuesAll = rowValuesAll.slice(1);
      }

      if (hasClassNames) {
         const className = rowValuesAll[0];
         classes.push(className);
         if (classNames.length === 0 || classNames.indexOf(className) < 0) {
            classNames.push(className);
         }
         rowValuesAll = rowValuesAll.slice(1);
      }

      const rowValues = rowValuesAll.map(v => Number.parseFloat(v.trim().replace(dec, ".")));

      if (rowValues.some(v => Number.isNaN(v))) {
         throw Error("Some of the values can not be converted to numbers, check your CSV file and try again.");
      }

      if (rowValues.length != ncols) {
         throw Error("Wrong number of values in row #" + r);
      }

      data.v.set(new Float64Array(rowValues), (r - startRow) * ncols);
   }

   // generate row labels if they are not provided
   if (!hasLabels || labels.length !== nrows) {
      labels = [];
      for (let i = 0; i < nrows; i++) {
         labels.push("O" + (i + 1));
      }
   }

   // handle variable labels
   let varlabels;
   let varvalues;
   if (header && header.length === ncols + startColumn) {
      varlabels = header.slice(startColumn);
   } else {
      varlabels = [];
      for (let i = 0; i < ncols; i++) {
         varlabels.push("V" + (i + 1));
      }
   }

   return {
      values: data.t(),
      header,
      labels,
      varlabels,
      varvalues,
      classes,
      classNames,
      sep,
      dec
   };
}


// function for downloading of PV-set
export function downloadResults(res, dataset, name, outliers, ncomp) {

   if (!outliers || !Array.isArray(outliers)) {
      outliers = [];
   }

   const nAll = dataset.values.nrows;
   const nOut = outliers.length;
   const n = nAll - nOut;
   const v = res.v[ncomp - 1];

   const hasClasses = res.classes && res.classes.length === n;
   const filler = hasClasses ? ['', '', '', '', ''] : ['', '', '', ''];
   let out = '';
   out += ['Data filename:', dataset.fileName, ...filler].join(dataset.sep) + '\n';
   out += ['Target class:', res.className, ...filler].join(dataset.sep) + '\n';
   out += ['Reference classes:',  hasClasses ? 'provided' : 'not provided', ...filler].join(dataset.sep) + '\n';
   out += ['Number of components:', ncomp, ...filler].join(dataset.sep) + '\n';
   out += ['Alpha:', (res.alpha/100).toFixed(3), ...filler].join(dataset.sep) + '\n';
   out += ['Limits estimator:', res.limType, ...filler].join(dataset.sep) + '\n';
   out += '\n';

   out += ['Nh:', v.Nh, ...filler].join(dataset.sep) + '\n';
   out += ['Nq:', v.Nq, ...filler].join(dataset.sep) + '\n';
   out += ['Nf:', v.Nf, ...filler].join(dataset.sep) + '\n';
   out += '\n';

   out += ['F crit (extremes):', v.eCrit, ...filler].join(dataset.sep) + '\n';
   out += ['F crit (outliers):', v.oCrit, ...filler].join(dataset.sep) + '\n';
   out += '\n';


   if (v.beta) {
      // there can be no removed outliers in this case
      out += ['Objects treated as from non-target class', '', ...filler].join(dataset.sep) + '\n';
      out += ['Objects:', nAll, ...filler].join(dataset.sep) + '\n';
      out += ['In:', v.dnum.in, ...filler].join(dataset.sep) + '\n';
      out += ['Out:', v.dnum.out, ...filler].join(dataset.sep) + '\n';
      out += ['Aliens:', v.anum.aliens, ...filler].join(dataset.sep) + '\n';
      out += ['External:', v.anum.external, ...filler].join(dataset.sep) + '\n';
      out += ['Specificity:', v.stat.specificity ? v.stat.specificity : '-', ...filler].join(dataset.sep) + '\n';
      out += ['Beta:', v.beta, ...filler].join(dataset.sep) + '\n';
      out += '\n';

      out += ['', 'object', 'class', 'role', 'decision', 'h/h0', 'q/q0', 'f'].join(dataset.sep) + '\n';

      for (let i = 0; i < nAll; i++) {
         out += [
            '',
            dataset.labels[i],
            res.classes[i],
            v.a[i],
            v.d[i],
            v.h[i] / v.h0,
            v.q[i] / v.q0,
            v.f[i]
         ].join(dataset.sep) + '\n';
      }

   } else {
      // there can be no removed outliers in this case
      out += ['Objects treated as from target class', '', ...filler].join(dataset.sep) + '\n';
      out += ['Objects:', nAll, ...filler].join(dataset.sep) + '\n';
      out += ['Removed:', nOut, ...filler].join(dataset.sep) + '\n';
      out += ['In:', v.dnum.in, ...filler].join(dataset.sep) + '\n';
      out += ['Out:', v.dnum.out, ...filler].join(dataset.sep) + '\n';
      out += ['Regular:', v.cnum.regular, ...filler].join(dataset.sep) + '\n';
      out += ['Extreme:', v.cnum.extreme, ...filler].join(dataset.sep) + '\n';
      out += ['Outliers:', v.cnum.outliers, ...filler].join(dataset.sep) + '\n';
      out += ['Sensitivity:', v.stat.sensitivity ? v.stat.sensitivity : '-', ...filler].join(dataset.sep) + '\n';
      out += '\n';

      out += ['', 'object', 'class', 'role', 'decision', 'h/h0', 'q/q0', 'f'].join(dataset.sep) + '\n';

      let j = 0;
      for (let i = 0; i < nAll; i++) {

         if (nOut > 0 && outliers.includes(i)) {
            out += [
               '',
               dataset.labels[i],
               '-',
               'removed',
               '-',
               '-',
               '-',
               '-'
            ].join(dataset.sep) + '\n';
            continue;
         }

         out += [
            '',
            dataset.labels[j],
            res.classes[j],
            v.c[j],
            v.d[j],
            v.h[j] / v.h0,
            v.q[j] / v.q0,
            v.f[j]
         ].join(dataset.sep) + '\n';
         j = j + 1;
      }
   }
   out += '\n';



   if (dataset.dec !== '.') {
      out = out.replace(/\./g, dataset.dec);
   }

   let hdr = ['DD-SIMCA results (' + name + ')', 'https://mda.tools/ddsimca/', ...filler].join(dataset.sep) + '\n\n';

   const blob = new Blob([hdr + out], { type: 'text/csv' });
   const url = window.URL.createObjectURL(blob)
   const a = document.createElement('a')
   a.setAttribute('href', url)
   a.setAttribute('download', `ddsimca-results-${name}.csv`);
   a.click()
}


// function for downloading of PV-set
export function downloadModel(pcamodel, simca, ncomp) {

   const blob = new Blob([JSON.stringify({
      pca: {
         hash: pcamodel.hash,
         class: pcamodel.class,
         ncomp: pcamodel.ncomp,
         nrows: pcamodel.nrows,
         ncols: pcamodel.ncols,
         center: pcamodel.center,
         scale: pcamodel.scale,
         lim: {
            classic: {
               Nh: Array.from(pcamodel.lim.classic.Nh),
               Nq: Array.from(pcamodel.lim.classic.Nq),
               h0: Array.from(pcamodel.lim.classic.h0),
               q0: Array.from(pcamodel.lim.classic.q0),
               Ng: Array.from(pcamodel.lim.classic.Ng),
               Nr: Array.from(pcamodel.lim.classic.Nr),
               g0: Array.from(pcamodel.lim.classic.g0),
               r0: Array.from(pcamodel.lim.classic.r0)
            },
            robust: {
               Nh: Array.from(pcamodel.lim.robust.Nh),
               Nq: Array.from(pcamodel.lim.robust.Nq),
               h0: Array.from(pcamodel.lim.robust.h0),
               q0: Array.from(pcamodel.lim.robust.q0),
               Ng: Array.from(pcamodel.lim.robust.Ng),
               Nr: Array.from(pcamodel.lim.robust.Nr),
               g0: Array.from(pcamodel.lim.robust.g0),
               r0: Array.from(pcamodel.lim.robust.r0)
            }
         },
         varlabels: pcamodel.varlabels,      // array with text labels for the variables
         exclvars: pcamodel.exclvars,        // array with indices of excluded variables - reserved for future use
         varvalues: pcamodel.varvalues,      // reserved for future use (wavelength, etc.) - reserved for future use
         vartitle: pcamodel.vartitle,        // title for variable axis — reserved for future use
         prep: [],                           // array with preprocessing methods — reserved for future use
         v: Array.from(pcamodel.v)           // array with all model outcomes (loadings, variances, etc)
      },
      simca: {
         ncomp: ncomp,
         nrows: simca.nrows,
         ncols: simca.ncols,
         alpha: simca.alpha,
         gamma: simca.gamma,
         limType: simca.limType,
         h0: Array.from(simca.h0),
         q0: Array.from(simca.q0),
         g0: Array.from(simca.g0),
         r0: Array.from(simca.r0),
         Nh: Array.from(simca.Nh),
         Nq: Array.from(simca.Nq),
         Nf: Array.from(simca.Nf),
         Ng: Array.from(simca.Ng),
         Nr: Array.from(simca.Nr),
         Ni: Array.from(simca.Nr),
         eCrit: Array.from(simca.eCrit),
         oCrit: Array.from(simca.oCrit),
         veCrit: Array.from(simca.veCrit),
         voCrit: Array.from(simca.voCrit),
         className: simca.className,
         hash: simca.hash,
      }
   })], { type: 'application/json' });

   const url = window.URL.createObjectURL(blob)
   const a = document.createElement('a')
   a.setAttribute('href', url)
   a.setAttribute('download', 'ddsimca-model.json');
   a.click()
}
