<script>
   /****************************************************************
   * SpecificityPlot                                               *
   * --------------------                                          *
   * shows plot with specificity values vs. number of PCs          *
   *****************************************************************/

   import { Axes, XAxis, YAxis, Box, Lines, Segments, Points } from 'svelte-plots-basic/2d';
   import { PNG_WIDTH, PNG_HEIGHT, PNG_RES, MARGINS, getSeries } from './Defaults.js';

   export let params;                     // JSON with main plot parameters
   export let simcares;                   // JSON with SIMCA results
   export let ncomp = 1;                  // number of components selected by user

   export let fileName = 'plot-specificity';
   export let pngWidth = PNG_WIDTH;
   export let pngHeight = PNG_HEIGHT;
   export let pngRes = PNG_RES;


   /*****************************************/
   /* Component code                        */
   /*****************************************/

   // constant plot parameters
   const showGrid = true;                  // logical, show grid or not
   const xLabel = 'Number of PCs';
   const yLabel = 'Specificity';
   const title = 'Specificity plot';


   // local copies of plot parameters
   let resID = simcares ? Object.keys(simcares)[0] : '';
   $: if (params && params.resID !== resID) resID = params.resID;
   $: nCompMax = simcares && resID ? simcares[resID].v.length : 1;
   $: s = getSeries(simcares, resID, 'specificity');
   $: limY = params && params.zoom === 'on' ? [s.yMin - 0.1, 1.05] : [-0.1, 1.1];
</script>

{#if params, s }
<Axes {title} {xLabel} {yLabel}
   {fileName} {pngWidth} {pngHeight} {pngRes}
   {limY}
   downloadLinks="hover"
   margins={MARGINS}
   limX={[0, nCompMax + 1]}
>

   <!-- selected component (vertical line) -->
   <Segments xStart={[ncomp]} xEnd={[ncomp]} yStart={[-1]} yEnd={[1.2]} lineColor='#a0a0a0' />

   <!-- data points -->
   <Lines xValues={s.x} yValues={s.y} lineWidth={1.5} lineColor='#2679B2' />
   <Points xValues={s.x} yValues={s.y} borderWidth={1.5} faceColor='#fff' borderColor='#2679B2'/>

   <!-- selected component (point) -->
   <Points
      xValues={[s.x[ncomp - 1]]}
      yValues={[s.y[ncomp - 1]]}
      markerSize={1.5}
      borderWidth={1.5}
      faceColor={s.lineColor}
      borderColor='#a0a0a0'
   />

   <XAxis {showGrid} slot="xaxis" />
   <YAxis {showGrid} slot="yaxis" />
   <Box slot="box" />
</Axes>
{/if}