import { Vector } from 'mdatools/arrays';
import { mean, variance } from 'mdatools/stat';
import { pnorm } from 'mdatools/distributions';

import { pcares_get } from './pca';

/**
 * Compute classification statistics.
 *
 * @param {Array} pred - array with predicted categories ('regular', 'extreme', 'outlier').
 * @param {Array} ref - array with reference class names for each object.
 * @param {string} className - name of the target class.
 *
 * @returns {Object} JSON with classification statistics.
 */
function get_stat(pred, ref, className) {
   if (!ref || !className || ref.length !== pred.length) return null;
   let TP = 0, FP = 0, TN = 0, FN = 0;
   for (let i = 0; i < pred.length; i++) {
      if (pred[i] === 'regular' && ref[i] === className) TP = TP + 1;
      if (pred[i] === 'regular' && ref[i] !== className) FP = FP + 1;
      if (pred[i] !== 'regular' && ref[i] === className) FN = FN + 1;
      if (pred[i] !== 'regular' && ref[i] !== className) TN = TN + 1;
   }

   return {
      TP, FP, TN, FN,
      'accuracy': (TP + TN) / (TP + TN + FP + FN),
      'specificity': TN / (TN + FP),
      'sensitivity': TP / (TP + FN),
   }
}

/**
 * Compute SIMCA predictions for a particular component for alternative dataset
 */
export function simca_predict_alt(pcares, simca, classes, labels, varlabels) {

   // get matrices with h and q distances
   const nrows = pcares.nrows;
   const ncomp = pcares.ncomp;
   const ncols = pcares.ncols;


   // we will make classification assuming that all objects are from alternative class
   const refClasses = Array.from({length: nrows}, () => ("alternative"));

   // get object with results for each component
   const res = new Array(ncomp);
   for (let a = 0; a < ncomp; a++) {

      // get usual results
      res[a] = simca_predict_single(simca, pcares, a, refClasses);

      let I = nrows;

      // get distances
      const f = res[a].f;
      const fcrit = simca.eCrit[a];

      // get DoF for full distance
      const k = simca.Nf[a];

      // 	Order f' such that f1'≤ f2'≤.... ≤ fI''
      const fv = new Vector(f);
      let fp = fv.sort();
      let ind = fv.sortind();

      // prepare array for roles
      const roles = Array.from({length: nrows}, () => ("alien"));;

      // Step 2. If Disc <0
      let Disc = -1;
      let n = 0;
      let m = 0;
      let d = 0;
      let M1 = 0;

      while (Disc < 0) {
         if (n > 0) {
            roles[ind.v[I - 1] - 1] = 'external';
            I = I - 1;
            ind = ind.slice(1, I);
            fp = fp.slice(1, I);
         }
         m = mean(fp);
         d = variance(fp, true);
         M1 = d / (m * m);
         Disc = 4 - 2 * k * M1;
         n = n + 1;
      }

      // Step 3. Calculate  x  by Eq. (18)
      const x = (2 + Math.sqrt(Disc)) / M1;

      // 	Calculate  s and  f'0 by Eq. (19)
      const s = (x - k);
      const f0 = m / x;

      // 	Calculate: z, h, r, p, Mz, Sz by Eq. (20)
      const z = fcrit / f0;
      const h = 1 - 2 * (k + s) * (k + 3 * s) / (3 * (k + 2 * s)**2);
      const r = (h - 1) * (1 - 3 * h);
      const p = (k + 2 * s) / (k + s)**2;
      const Mz = 1 + h * p * (h - 1 - 0.5 * (2 - h) * r * p);
      const Sz = h * Math.sqrt(2 * p) * (1 + 0.5 * r * p);

      // Step 3.	If α is given then calculate β by Eq. (21)
      const beta = pnorm((Math.pow(z / (k + s), h) - Mz) / Sz);

      res[a].beta = beta;
      res[a].s = s;
      res[a].k = k;
      res[a].Mz = Mz;
      res[a].Sz = Sz;
      res[a].m = m;
      res[a].f0 = f0;
      res[a].hz = h;
      res[a].anum = {'aliens': I, 'external': nrows - I};
      res[a].a = roles;
   }

   return {
      class: ['simcares'],
      v: res,
      className: simca.className,
      alpha: simca.alpha,
      limType: simca.limType,
      ncomp: simca.ncomp,
      nrows: nrows,
      ncols: ncols,
      classes: classes,
      classNames: classes && classes.length > 0 ? [...new Set(classes)] : null,
      labels: labels,
      varlabels: varlabels
   }
}

/**
 * Compute SIMCA predictions for a particular component
 */
export function simca_predict_single(simca, pcares, a, classes) {

   // get object distances
   const f = pcares_get(pcares, (simca.limType === 'robust' ? 'fdistr' : 'fdistc'), a + 1);
   const h = pcares_get(pcares, 'hdist', a + 1);
   const q = pcares_get(pcares, 'qdist', a + 1);

   // get variable distances
   const i = pcares_get(pcares, (simca.limType === 'robust' ? 'idistr' : 'idistc'), a + 1);
   const g = pcares_get(pcares, 'gdist', a + 1);
   const r = pcares_get(pcares, 'rdist', a + 1);

   const nrows = f.length;

   // get object distance parameters
   const h0 = simca.h0[a];
   const Nh = simca.Nh[a];
   const q0 = simca.q0[a];
   const Nq = simca.Nq[a];
   const Nf = simca.Nf[a];

   // get variable distance parameters
   const g0 = simca.g0[a];
   const Ng = simca.Ng[a];
   const r0 = simca.r0[a];
   const Nr = simca.Nr[a];
   const Ni = simca.Ni[a];

   // get critical limits (for objects and variables)
   const oCrit = simca.oCrit[a];
   const eCrit = simca.eCrit[a];
   const voCrit = simca.voCrit[a];
   const veCrit = simca.veCrit[a];

   // prepare array for object categories
   const c = new Array(nrows);
   const d = new Array(nrows);
   const cnum = {'regular': 0, 'extreme': 0, 'outliers': 0};
   const dnum = {'in': 0, 'out': 0};

   for (let k = 0; k < nrows; k++) {
      const fk = f[k];
      if (fk < eCrit) {
         c[k] = 'regular';
         d[k] = 'in';
         cnum.regular += 1;
         dnum.in += 1;
      } else if (fk > oCrit) {
         c[k] = 'outlier';
         d[k] = 'out';
         cnum.outliers += 1;
         dnum.out += 1;
      } else {
         c[k] = 'extreme';
         d[k] = 'out';
         cnum.extreme += 1;
         dnum.out += 1;
      }
   }
   const stat =  classes && classes.length === nrows ? get_stat(c, classes, simca.className) : null
   return {
      h, h0, Nh, q, q0, Nq, f, Nf, eCrit, oCrit, c, d, cnum, dnum, stat,
      g, g0, Ng, r, r0, Nr, i, Ni, veCrit, voCrit
   };
}

/**
 * Compute SIMCA predictions.
 *
 * @param {Object} pcares - JSON with predicted PCA results.
 * @param {Object} simca - JSON with SIMCA parameters.
 * @param {Array} classes - array with reference class names for each object.
 * @param {Array} labels - array with reference labels for each object.
 * @param {Array} varlabels - array with reference labels for each variable.
 *
 * @returns {Object} JSON with SIMCA parameters.
 */
export function simca_predict(pcares, simca, classes, labels, varlabels) {

   // get matrices with h and q distances
   const nrows = pcares.nrows;
   const ncomp = pcares.ncomp;
   const ncols = pcares.ncols;

   // we will make classification assuming that all objects are from alternative class
   const refClasses = Array.from({length: nrows}, () => (simca.className))

   // get object with results for each component
   const res = new Array(ncomp);
   for (let a = 0; a < ncomp; a++) {
      res[a] = simca_predict_single(simca, pcares, a, refClasses);
   }
   return {
      class: ['simcares'],
      v: res,
      className: simca.className,
      alpha: simca.alpha,
      limType: simca.limType,
      ncomp: simca.ncomp,
      nrows: nrows,
      ncols: ncols,
      classes: classes,
      labels: labels,
      varlabels: varlabels
   }
}
