<script>
   /*************************************************************
   * DataLoader for test set                                    *
   * -------------------------                                  *
   * loads test set from CSV file                               *
   **************************************************************/

   import { pca_predict } from '../wasm/pca';
   import { parseCSV } from '../utils.js';

   import DatasetWidget from './DatasetWidget.svelte';

   import FileSelector from '../../../shared/controls/FileSelector.svelte';
   import Spinner from '../../../shared/controls/Spinner.svelte';
   import Select from '../../../shared/controls/Select.svelte';
   import Container from '../../../shared/controls/Container.svelte';
   import Button from '../../../shared/controls/Button.svelte';

   export let WASM;
   export let dataset;
   export let model;
   export let pcares;
   export let outliers;

   export let outliersOld = [];

   let dataFile;                    // CSV file
   let sep = ',';                   // seperator between values
   let hasHeader = 'yes';           // first row contains header (variable names)
   let hasLabels = 'no';            // first column contains row labels
   let hasClassLabels = 'no';       // next column contains class labels
   let promise;

   // app status for handling and showing errors
   let status = 'neutral';
   let errMsg = '';

   // possible delimeters
   const sepOptions = [',', ';'];

   function predict(data, outliers) {
      promise = WASM()
         .then(Module => {
            const res = pca_predict(Module, model, data, outliers);
            pcares = res;
         })
         .catch(e =>{
            pcares = null;
            status = 'error';
            errMsg = e;
         });
   };


   // gets CSV content as one large string, parse it into matrix
   function setData(strData) {
      try{
         const d = parseCSV(
            strData,
            sep,
            hasHeader === 'yes',
            hasLabels === 'yes',
            hasClassLabels === 'yes'
         );

         if (d.values.nrows < 1 ) {
            throw new Error('Dataset must have at least 1 row.');
         }

         if (d.values.ncols !== model.ncols) {
            throw new Error('Dataset has different number of columns than the training set.');
         }

         d['fileName'] = dataFile.name;
         dataset = d;
         predict(d.values, []);
         errMsg = ''
         status = 'success';
      } catch (e) {
         dataset = null;
         status = 'error';
         errMsg = e;
      }
   }

   // promise for reading the CSV file
   function readFile (e) {
      // // old code - deprecated
      // const reader = new FileReader();
      // reader.onload = () => setData(reader.result);
      // reader.readAsBinaryString(dataFile);
      // const reader = new FileReader();
      // reader.onload = () => {
      //    const arrayBuffer = reader.result;
      //    const binaryString = String.fromCharCode.apply(null, new Uint8Array(arrayBuffer));
      //    setData(binaryString);
      // };
      // reader.readAsArrayBuffer(dataFile);

      const reader = new FileReader();
      reader.onload = () => {
         setData(reader.result);
      };
      reader.readAsText(dataFile);
   }



   // reset whole component to initial state
   function reset () {
      dataFile = null;
   }

   // reactive expression for tracking changes of 'dataFile'
   $: if (dataFile === null) {
      // if data file is empty reset status and error messages
      dataset = null;
   } else {
      // if not just remove error messages if any
      status = 'neutral';
      errMsg = '';
   }

   // reactive expression for tracking changes of 'sep'
   $: if (sep) {
      // if separator was changed remove error message
      status = 'neutral';
      errMsg = '';
   }

   $: if (dataset && outliers !== outliersOld) {
      try{
         predict(dataset.values, outliers);
         errMsg = ''
         status = 'success';
         outliersOld = outliers;
      } catch (e) {
         dataset = null;
         status = 'error';
         errMsg = e;
      }
   }

</script>

{#if !dataset }

   <!-- show if data is not loaded -->
   <!-- reset component if dataFile is changed inside -->
   {#key dataFile === undefined}
   <Container>
      <FileSelector id="dataFile" message="Select CSV file with dataset" bind:file={dataFile} />
   </Container>
   {/key}

   <!-- show if file is selected but data is not loaded -->
   {#if dataFile}
   <Container label="Delimiter:">
      <Select id="sep" bind:value={sep} options={sepOptions} />
   </Container>
   <Container label="Header:">
      <Select id="hasHeader" bind:value={hasHeader} options={['yes', 'no']} />
   </Container>
   <Container label="Row labels:">
      <Select id="hasLabels" bind:value={hasLabels} options={['yes', 'no']} />
   </Container>
   <Container label="Class labels:">
      <Select id="hasLabels" bind:value={hasClassLabels} options={['yes', 'no']} />
   </Container>
   <Container label="&nbsp;">
      <Button on:click={readFile}>Load</Button>
   </Container>
   {/if}

{:else}


{#if promise}

   <!-- calculation is in process -->
   {#await promise}
      <Container>
         <Spinner />
         <p>Loading data and apply model...</p>
      </Container>

   {:then value}
      <!-- status if succeed -->
      {#if pcares !== null}
      <!-- dataset is loaded - show info and cancel button -->
      <DatasetWidget {dataset} on:click={reset} />
      {/if}

   {:catch error}
      <!-- error message if failed -->
      <Container status="error">
         <p>{error}</p>
      </Container>
   {/await}

{/if}


{/if}

<!-- error message -->
{#if status === 'error' || errMsg}
<Container status="error">
   <p>{errMsg}</p>
</Container>
{/if}

