<script>
   /*************************************************************
   * DataLoader for training set                                *
   * -------------------------                                  *
   * loads training set from CSV file                           *
   **************************************************************/

   import DatasetWidget from '../shared/DatasetWidget.svelte';
   import { parseCSV } from '../utils.js';

   import FileSelector from '../../../shared/controls/FileSelector.svelte';
   import Select from '../../../shared/controls/Select.svelte';
   import Container from '../../../shared/controls/Container.svelte';
   import Button from '../../../shared/controls/Button.svelte';


   // calibration set
   export let dataset;

   let dataFile;                    // CSV file
   let sep = ',';                   // separator between values
   let hasHeader = 'yes';           // first row contains header (variable names)
   let hasLabels = 'no';            // firs column contains row labels

   // app status for handling and showing errors
   let status = 'neutral';
   let errMsg = '';

   // possible delimeters
   const sepOptions = [',', ';'];

   // converts array buffer to long string
   function arrayBufferToString(buffer) {
      const uint8Array = new Uint8Array(buffer);
      return new TextDecoder().decode(uint8Array);
   }

   // gets CSV content as one large string, parse it into matrix
   function setData(strData) {
      try{
         const d = parseCSV(
            strData,
            sep,
            hasHeader === 'yes',
            hasLabels === 'yes',
            true // calibration set must have classNames
         );

         if (d.values.nrows < 3 || d.values.ncols < 3) {
            throw new Error('Calibration set must have at least 3 rows and 3 columns');
         }

         if (d.classNames.length !== 1) {
            throw new Error('Calibration set must have a column filled with target class name.');
         }

         d['fileName'] = dataFile.name;
         dataset = d;
         errMsg = ''
         status = 'success';
      } catch (e) {
         console.error(e)
         dataset = null;
         status = 'error';
         errMsg = e;
      }
   }

   // promise for reading the CSV file
   function readFile (e) {
      // // old code - deprecated
      // const reader = new FileReader();
      // reader.onload = () => setData(arrayBufferToString(reader.result));
      // reader.readAsArrayBuffer(dataFile);

      // const reader = new FileReader();
      // reader.onload = () => {
      //    const arrayBuffer = reader.result;
      //    const binaryString = String.fromCharCode.apply(null, new Uint8Array(arrayBuffer));
      //    setData(binaryString);
      // };
      // reader.readAsArrayBuffer(dataFile);
      const reader = new FileReader();
      reader.onload = () => {
         setData(reader.result);
      };
      reader.readAsText(dataFile);
   }

   // reset whole component to initial state
   function reset () {
      dataset = null;
      dataFile = null;
   }

   // reactive expression for tracking changes of 'dataFile'
   $: if (dataFile === null) {
      // if data file is empty reset status and error messages
      reset();
   } else {
      // if not just remove error messages if any
      status = 'neutral';
      errMsg = '';
   }

   // reactive expression for tracking changes of 'sep'
   $: if (sep) {
      // if separator was changed remove error message
      status = 'neutral';
      errMsg = '';
   }

</script>

{#if !dataset }

   <!-- show if data is not loaded -->
   <!-- reset component if dataFile is changed inside -->
   {#key dataFile === undefined}
   <Container>
      <FileSelector id="dataFile" message="Select CSV file with training set" bind:file={dataFile} />
   </Container>
   {/key}

   <!-- show if file is selected but data is not loaded -->
   {#if dataFile}
   <Container label="Delimiter:">
      <Select id="sep" bind:value={sep} options={sepOptions} />
   </Container>
   <Container label="Header:">
      <Select id="hasHeader" bind:value={hasHeader} options={['yes', 'no']} />
   </Container>
   <Container label="Row labels:">
      <Select id="hasLabels" bind:value={hasLabels} options={['yes', 'no']} />
   </Container>
   <Container label="&nbsp;">
      <Button on:click={readFile}>Load</Button>
   </Container>
   {/if}

{:else}
   <DatasetWidget {dataset} on:click={reset} />
{/if}

<!-- error message -->
{#if status === 'error' || errMsg}
<Container status="error">
   <p>{errMsg}</p>
</Container>
{/if}
