<script>
   /****************************************************************
   * SensitivityPlot                                               *
   * --------------------                                          *
   * shows plot with sensitivity values vs. number of PCs          *
   *****************************************************************/

   import { Axes, XAxis, YAxis, Box, Lines, Segments, Legend, Rectangles, Points } from 'svelte-plots-basic/2d';
   import { PNG_WIDTH, PNG_HEIGHT, PNG_RES, MARGINS, getSeries } from './Defaults.js';

   export let params;                     // JSON with main plot parameters
   export let simcares;                   // JSON with SIMCA results
   export let ncomp = 1;                  // number of components selected by user

   export let fileName = 'plot-sensitivity';
   export let pngWidth = PNG_WIDTH;
   export let pngHeight = PNG_HEIGHT;
   export let pngRes = PNG_RES;


   /*****************************************/
   /* Component code                        */
   /*****************************************/

   // constant plot parameters
   const showGrid = true;                  // logical, show grid or not
   const xLabel = 'Number of PCs';
   const yLabel = 'Sensitivity';
   const title = 'Sensitivity plot';

   // parameters depending on the provided results
   let resIDs, amargin, aline, nCompMax;
   $: if (simcares) {
      resIDs = Object.keys(simcares);
      const nObj = simcares[resIDs[0]].nrows;
      const alpha = simcares[resIDs[0]].alpha;
      nCompMax = simcares[resIDs[0]].v.length;
      aline = 1 - alpha/100;
      amargin = 2 * Math.sqrt(alpha/100 * (1 - alpha/100) / nObj);
   } else {
      resIDs = null;
      aline = 0;
      amargin = 0;
   }

   let series, legendItems;
   $: if (resIDs && params && params.resID) {
      series = params.resID === 'both' ?
         resIDs.map(id => getSeries(simcares, id, 'sensitivity')) :
         [getSeries(simcares, params.resID, 'sensitivity')];
      legendItems = series.map(s => s.legendItem);
   } else {
      series = null;
      legendItems = null;
   }

   // reactive computation of confidence area
   $: atop = amargin + aline;
   $: abottom = aline - amargin;

   // reactive computation of y limits
   $: limYMin = params && params.zoom === 'on' ? abottom - 0.5 * amargin : -0.1;
   $: limYMax = params && params.zoom === 'on' ? atop + 0.5 * amargin : 1.2;

</script>

{#if series }
<Axes {title} {xLabel} {yLabel}
   {fileName} {pngWidth} {pngHeight} {pngRes}
   downloadLinks="hover"
   margins={MARGINS}
   limX={[0, nCompMax + 1]}
   limY={[limYMin, limYMax]}>

   <!-- area with possible sensitivity -->
   <Rectangles left={[0]} width={[nCompMax + 1]} top={[atop]} height={[2 * amargin]} faceColor='#00000010'/>
   <Segments xStart={[0]} xEnd={[nCompMax + 1]} yStart={[aline]} yEnd={[aline]} lineColor='#00000050' lineType={2}/>

   <!-- selected component -->
   <Segments xStart={[ncomp]} xEnd={[ncomp]} yStart={[-1]} yEnd={[1.2]} lineColor='#a0a0a0' />

   <!-- data points -->
   {#each series as s}
   <Lines xValues={s.x} yValues={s.y} lineWidth={1.5} lineColor={s.lineColor} />
   <Points xValues={s.x} yValues={s.y} borderWidth={1.5} faceColor={s.faceColor} borderColor={s.lineColor}/>

   <!-- selected component (point) -->
   <Points
      xValues={[s.x[ncomp - 1]]}
      yValues={[s.y[ncomp - 1]]}
      markerSize={1.5}
      borderWidth={1.5}
      faceColor={s.lineColor}
      borderColor='#a0a0a0'
   />
   {/each}

   {#if legendItems && legendItems.length > 0}
   <Legend items={legendItems} position='topright'/>
   {/if}

   <XAxis {showGrid} slot="xaxis" />
   <YAxis {showGrid} slot="yaxis" />
   <Box slot="box" />
</Axes>
{/if}