<script>
   /**************************************************************/
   /* Svelte component for VariableDistancePlot settings         */
   /**************************************************************/

   import Container from '../controls/Container.svelte';
   import Select from '../controls/Select.svelte';

   export let params;
   export let simcares;

   let scope = 'variables';
   let distType = 'i';
   let showLabels = 'hide';
   let resID = 'both';

   $: params = {
      scope,
      distType,
      showLabels,
      resID
   }
</script>

{#if simcares  }
<Container label="">
   <p class="title">Distance plot (variables)</p>
</Container>
{#if Object.keys(simcares).length > 1 }
<Container label='Results:'>
   <Select id='resType' bind:value={resID} options={[...Object.keys(simcares), 'both']} />
</Container>
{/if}
<Container label='Distances:'>
   <Select id='distances' bind:value={distType} options={['g', 'r', 'i']} />
</Container>
<Container label='Labels:'>
   <Select id='showLabels' bind:value={showLabels} options={['hide', 'show']} />
</Container>
{/if}

