<script>
   /****************************************************************
   * SelectivityPlot                                               *
   * --------------------                                          *
   * shows plot with number of alien samples for different alpha   *
   *****************************************************************/

   import { pnorm, qchisq } from 'mdatools/distributions';
   import { Vector } from 'mdatools/arrays';
   import { Axes, XAxis, YAxis, Box, Lines, Segments, Points } from 'svelte-plots-basic/2d';
   import { PNG_WIDTH, PNG_HEIGHT, PNG_RES, MARGINS, LINE_COLORS, FACE_COLORS, getSeries } from './Defaults.js';

   export let params;                                 // JSON with main plot parameters
   export let simcares;                               // JSON with SIMCA results
   export let showGrid = true;                        // logical, show grid or not
   export let yLabel = 'Selectivity (1 - β)';         // label for y axis
   export let ncomp = 1;                              // number of components selected by user

   // plot saving parameters
   export let fileName = 'plot-aliens';
   export let pngWidth = PNG_WIDTH;
   export let pngHeight = PNG_HEIGHT;
   export let pngRes = PNG_RES;


   /*****************************************/
   /* Component code                        */
   /*****************************************/


   export function getPlotSeries(resobj, ncomp, vsVar) {
      if (!resobj || ncomp < 1) return;

      // compute selectivity vs theoretical values of alpha for fixed ncomp
      if (vsVar === 'β vs. α') {
         //const n = res.h.length;
         const res = resobj.v[ncomp - 1];
         const f0 = res.f0;
         const k = res.k;
         const s = res.s;
         const h = res.hz;
         const Sz = res.Sz;
         const Mz = res.Mz;

         let n = 181;
         const x = Vector.ones(n);
         const y = Vector.ones(n);
         let sel = 0;

         const norm1 = 1 / (k + s);
         const norm2 = 1 / Sz;

         function getbeta(a) {
            const fcrit = qchisq(1 - a, k);
            const z = fcrit / f0;
            return pnorm((Math.pow(z * norm1, h) - Mz) * norm2);
         }

         // alpha from 0 to 0.1
         let i = 1;
         for (let a = 0.001; a < 0.1; a = a + 0.002) {
            const b = getbeta(a);
            x.v[i] = b;
            y.v[i] = 1 - a;
            sel = sel + b * 0.002
            i = i + 1;
         }

         for (let a = 0.1; a < 0.9; a = a + 0.01) {
            const b = getbeta(a);
            x.v[i] = b;
            y.v[i] = 1 - a;
            sel = sel + b * 0.01
            i = i + 1;
         }

         for (let a = 0.9; a < 1.0; a = a + 0.002) {
            const b = getbeta(a);
            x.v[i] = b;
            y.v[i] = 1 - a;
            sel = sel + b * 0.002
            i = i + 1;
         }

         x.v[i] = 0;
         y.v[i] = 0;

         return [x, y, 1 - sel];
      }

      // compute selectivity vs ncomp for fixed alpha
      const nCompMax = resobj.v.length;
      const x = Array(nCompMax);
      const y = Array(nCompMax);
      for (let i = 0; i < nCompMax; i++) {
         x[i] = i + 1;
         y[i] = 1 - resobj.v[i].beta;
      }
      return [x, y, 0];
   }



   // local copies of Plot parameters
   let resID = simcares ? Object.keys(simcares)[0] : '';
   let vsVar = 'β vs. α';

   $: resobj = simcares && resID ? simcares[resID] : null;
   $: if (params && params.vsVar) vsVar = params.vsVar;


   $: [x, y, sel] = resobj && ncomp > 0 ? getPlotSeries(resobj, ncomp, vsVar) : [null, null];
   $: limX = vsVar === 'β vs. α' ? [-0.05, 1.05] : [0, resobj.v.length + 1];
   $: yLabel = vsVar === 'β vs. α' ? 'Expected sensitivity, 1 - α' : 'Selectivity, 1 - β';
   $: xLabel = vsVar === 'β vs. α' ? '1 – selectivity, β' : 'Number of PCs';
   $: title = vsVar === 'β vs. α' ? 'Selectivity plot (A = ' + ncomp + ', AUC = ' + sel.toFixed(4) + ')' : 'Selectivity plot (α = ' + resobj.alpha/100 + ')';

</script>

{#if x && y }
<Axes {title} {xLabel} {yLabel} {limX}
   downloadLinks="hover"
   margins={MARGINS}
   limY={[-0.05, 1.05]}
   {fileName} {pngWidth} {pngHeight} {pngRes}
>

   {#if vsVar === 'β vs. ncomp'}
   <!-- selected component (line )-->
   <Segments xStart={[ncomp]} xEnd={[ncomp]} yStart={[-1]} yEnd={[1.2]} lineColor='#a0a0a0' />
   {:else}
   <!-- 50% line -->
   <Segments xStart={[0]} xEnd={[1]} yStart={[0]} yEnd={[1]} lineColor='#a0a0a0' lineType={3} />
   {/if}

   <!-- data points -->
   <Lines xValues={x} yValues={y} lineWidth={1.5} lineColor={LINE_COLORS['alt']} />

   <!-- selected component (point) -->
   {#if vsVar === 'β vs. ncomp'}
   <Points xValues={x} yValues={y} borderWidth={1.5} faceColor={FACE_COLORS['alt']} borderColor={LINE_COLORS['alt']}/>
   <Points
      xValues={[x[ncomp - 1]]}
      yValues={[y[ncomp - 1]]}
      markerSize={1.5}
      borderWidth={1.5}
      faceColor={LINE_COLORS['alt']}
      borderColor='#a0a0a0'
   />
   {/if}

   <XAxis {showGrid} slot="xaxis" />
   <YAxis {showGrid} slot="yaxis" />
   <Box slot="box" />
</Axes>
{/if}