<script>
   /*************************************************************
   * AppPlots component for training app                        *
   * -------------------------------                            *
   * show selection of plot for training of SIMCA model         *
   **************************************************************/

   // import plot and plot controls components
   import AppPlots from '../shared/AppPlots.svelte';

   import SensitivityPlot from '../../../shared/plots/SensitivityPlot.svelte';
   import ExtremePlot from '../../../shared/plots/ExtremePlot.svelte';
   import AcceptancePlot from '../../../shared/plots/AcceptancePlot.svelte';
   import DistancePlot from '../../../shared/plots/DistancePlot.svelte';
   import DFPlot from '../../../shared/plots/DFPlot.svelte';

   import SensitivityPlotControls from '../../../shared/plots/SensitivityPlotControls.svelte';
   import ExtremePlotControls from '../../../shared/plots/ExtremePlotControls.svelte';
   import AcceptancePlotControls from '../../../shared/plots/AcceptancePlotControls.svelte';
   import DistancePlotControls from '../../../shared/plots/DistancePlotControls.svelte';
   import VariableDistancePlotControls from '../../../shared/plots/VariableDistancePlotControls.svelte';
   import DFPlotControls from '../../../shared/plots/DFPlotControls.svelte';
   import ImportancePlotControls from '../../../shared/plots/ImportancePlotControls.svelte';


   // input parameters
   export let simcares;
   export let ncomp;

   // array with list of plots and their components
   // parameter "scope" tells parent AppPlots component whether click events must be handled:
   // - for "objects" - it triggers click events on plots as selection of objects and mark them as outliers
   // - for "variables" - it triggers click events on plots as selection of variables (no more actions so far)
   // - for "none" - the click events will be ignored
   const options = [
      {label: 'Acceptance', scope: 'objects', plotComponent: AcceptancePlot, controlsComponent: AcceptancePlotControls},
      {label: 'Distance (obj)', scope: 'objects', plotComponent: DistancePlot, controlsComponent: DistancePlotControls},
      {label: 'Distance (var)', scope: 'variables', plotComponent: DistancePlot, controlsComponent: VariableDistancePlotControls},
      {label: 'DoF', scope: 'none', plotComponent: DFPlot, controlsComponent: DFPlotControls},
      {label: 'Extremes', scope: 'none', plotComponent: ExtremePlot, controlsComponent: ExtremePlotControls},
      {label: 'Sensitivity', scope: 'none', plotComponent: SensitivityPlot, controlsComponent: SensitivityPlotControls},
      {label: 'Importance', scope: 'variables', plotComponent: AcceptancePlot, controlsComponent: ImportancePlotControls},
   ]
</script>


<AppPlots on:setoutlier on:plotoutlier {simcares} {options} {ncomp} />