<script>
   /*************************************************************
   * AppPlots component for alternative set                     *
   * --------------------------------------                     *
   * show selection of plot for testing of SIMCA model          *
   **************************************************************/

   // import plot and plot controls components
   import AppPlots  from '../shared/AppPlots.svelte';

   import AcceptancePlot  from '../../../shared/plots/AcceptancePlot.svelte';
   import SpecificityPlot from '../../../shared/plots/SpecificityPlot.svelte';
   import AliensPlot from '../../../shared/plots/AliensPlot.svelte';
   import DistancePlot from '../../../shared/plots/DistancePlot.svelte';
   import SelectivityPlot from '../../../shared/plots/SelectivityPlot.svelte';

   import AltAcceptancePlotControls from '../../../shared/plots/AltAcceptancePlotControls.svelte';
   import SpecificityPlotControls from '../../../shared/plots/SpecificityPlotControls.svelte';
   import AliensPlotControls from '../../../shared/plots/AliensPlotControls.svelte';
   import DistancePlotControls from '../../../shared/plots/DistancePlotControls.svelte';
   import VariableDistancePlotControls from '../../../shared/plots/VariableDistancePlotControls.svelte';
   import ImportancePlotControls from '../../../shared/plots/ImportancePlotControls.svelte';
   import SelectivityPlotControls from '../../../shared/plots/SelectivityPlotControls.svelte';


   // Input parameters
   export let simcares;
   export let ncomp;


   // array with list of plots and their components
   // parameter "scope" tells parent AppPlots component whether click events must be handled:
   // - for "objects" - it triggers click events on plots as selection of objects and mark them as outliers
   // - for "variables" - it triggers click events on plots as selection of variables (no more actions so far)
   // - for "none" - the click events will be ignored
   let options = [
      {label: 'Acceptance', scope: 'none', plotComponent: AcceptancePlot, controlsComponent: AltAcceptancePlotControls},
      {label: 'Distance (obj)', scope: 'none', plotComponent: DistancePlot, controlsComponent: DistancePlotControls},
      {label: 'Distance (var)', scope: 'none', plotComponent: DistancePlot, controlsComponent: VariableDistancePlotControls},
      {label: 'Specificity', scope: 'none', plotComponent: SpecificityPlot, controlsComponent: SpecificityPlotControls},
      {label: 'Aliens', scope: 'none', plotComponent: AliensPlot, controlsComponent: AliensPlotControls},
      {label: 'Importance', scope: 'none', plotComponent: AcceptancePlot, controlsComponent: ImportancePlotControls},
      {label: 'Selectivity', scope: 'none', plotComponent: SelectivityPlot, controlsComponent: SelectivityPlotControls},
   ];

</script>

{#if options}
<AppPlots {simcares} {options} {ncomp} />
{/if}