<script>
   /*************************************************************
   * ResultsTable                                               *
   * -------------------------                                  *
   * shows table with SIMCA results for training and PV-sets    *
   **************************************************************/

   // main properties of the whole app
   export let simcares;
   export let ncomp;

   $: cal = simcares ? simcares.cal.v[ncomp - 1] : null;
   $: pv = simcares ? simcares.pv.v[ncomp - 1] : null;
</script>


{#if cal && pv}
<table>
   <tr>
      <th></th>
      <th>Cal</th>
      <th>PV</th>
   </tr>
   <tr>
      <td class="label">Objects</td>
      <td>{simcares.cal.nrows}</td>
      <td>{simcares.pv.nrows}</td>
   </tr>
   <tr>
      <td class="label">Extremes</td>
      <td>{cal.cnum.extreme}</td>
      <td>{pv.cnum.extreme}</td>
   </tr>
   <tr>
      <td class="label">Outliers</td>
      <td>{cal.cnum.outliers}</td>
      <td>{pv.cnum.outliers}</td>
   </tr>
   <tr>
      <td class="label">Sensitivity</td>
      <td>{cal.stat.sensitivity.toFixed(2)}</td>
      <td>{pv.stat.sensitivity.toFixed(2)}</td>
   </tr>
</table>
{/if}

