<script>
   /****************************************************************
   * DFPlot                                                        *
   * --------------------                                          *
   * shows plot with number of degrees of freedom vs number of PCs *
   *****************************************************************/

   import { max } from 'mdatools/stat';
   import { Vector } from 'mdatools/arrays';
   import { Axes, XAxis, YAxis, Box, Lines, Legend, Segments, Points } from 'svelte-plots-basic/2d';
   import { PNG_WIDTH, PNG_HEIGHT, PNG_RES, MARGINS } from './Defaults.js';

   export let params;                     // JSON with main plot parameters
   export let simcares;                   // JSON with SIMCA results
   export let ncomp = 1;                  // number of components selected by user

   export let fileName = 'plot-dof';
   export let pngWidth = PNG_WIDTH;
   export let pngHeight = PNG_HEIGHT;
   export let pngRes = PNG_RES;

   /*****************************************/
   /* Component code                        */
   /*****************************************/

   // constant plot parameters
   const showGrid = true;                  // logical, show grid or not
   const xLabel = 'Number of PCs';
   const yLabel = 'Degrees of freedom, Nu';
   const title = 'DoF plot';

   // colors for series
   const COLORS = ['#2679B2', '#D22C2F']; // Nh, Nq

   // local copies of plot parameters
   let item = 'both';

   // reactive checks of local copies of plot parameters
   $: if (params && params.item && params.item !== item) item = params.item;

   let legendItems = [];
   $: if (item) {
      legendItems = [];
      if (item === 'Nh' || item === 'both')
         legendItems.push({'label': 'Nh', 'borderColor': COLORS[0], 'marker': 1, 'borderWidth': 2});
      if (item === 'Nq' || item === 'both')
         legendItems.push({'label': 'Nq', 'borderColor': COLORS[1], 'marker': 1, 'borderWidth': 2});
   } else {
      legendItems = null;
   };

   let Nh, Nq, xMax, yMax, x;
   $: if (simcares) {
      const res = simcares[Object.keys(simcares)[0]];
      const ncomp = res.v.length;
      Nh = Vector.zeros(ncomp);
      Nq = Vector.zeros(ncomp);
      x = Vector.zeros(ncomp);
      let NhMax = 0;
      let NqMax = 0;
      for (let a = 0; a < ncomp; a++) {
         const Nha = res.v[a].Nh;
         const Nqa = res.v[a].Nq;
         Nh.v[a] = Nha;
         Nq.v[a] = Nqa;
         x.v[a] = a + 1;
         NhMax = Math.max(NhMax, Nha);
         NqMax = Math.max(NqMax, Nqa);
      }

      xMax = ncomp;
      yMax = {'both': 1.2 * Math.max(NhMax, NqMax), 'Nh': 1.2 * NhMax, 'Nq': 1.2 * NqMax};
   } else {
      x = null;
      Nh = null;
      Nq = null;
   }
</script>

{#if params, x, Nh, Nq }
<Axes {title} {xLabel} {yLabel}
   downloadLinks="hover"
   margins={MARGINS}
   limX={[0, xMax + 1]}
   limY={[-1, yMax[item]]}
   {fileName} {pngWidth} {pngHeight} {pngRes}
>

   <Segments xStart={[ncomp]} xEnd={[ncomp]} yStart={[-1]} yEnd={[yMax[item]]} lineColor='#00000040' />

   {#if item === 'Nh' || item === 'both'}
   <Lines xValues={x} yValues={Nh} lineWidth={1.5} lineColor={COLORS[0]} />
   <Points xValues={x} yValues={Nh} borderWidth={1.5} faceColor='#fff' borderColor={COLORS[0]}/>
   <Points xValues={[x.v[ncomp - 1]]} yValues={[Nh.v[ncomp - 1]]} borderWidth={4} faceColor={COLORS[0]} borderColor={COLORS[0]}/>
   {/if}

   {#if item === 'Nq' || item === 'both'}
   <Lines xValues={x} yValues={Nq} lineWidth={1.5} lineColor={COLORS[1]} />
   <Points xValues={x} yValues={Nq} borderWidth={1.5} faceColor='#fff' borderColor={COLORS[1]}/>
   <Points xValues={[x.v[ncomp - 1]]} yValues={[Nq.v[ncomp - 1]]} borderWidth={4} faceColor={COLORS[1]} borderColor={COLORS[1]}/>
   {/if}

   <Legend items={legendItems} />
   <XAxis {showGrid} slot="xaxis" />
   <YAxis {showGrid} slot="yaxis" />
   <Box slot="box" />
</Axes>
{/if}