<script>
   /**************************************************************/
   /* Svelte component for AcceptancePlot settings               */
   /**************************************************************/

   import Container from '../controls/Container.svelte';
   import Select from '../controls/Select.svelte';

   export let params;
   export let simcares;

   let isAlt = true;
   let scope = 'objects';
   let transType = 'no';
   let showLabels = 'hide';
   let resID = simcares ? Object.keys(simcares)[0] : '';
   let colorBy = 'roles';

   $: params = {
      isAlt,
      scope,
      transType,
      showLabels,
      colorBy,
      resID
   }

   $: colorByValid = simcares['alt'].classNames &&
      simcares['alt'].classNames.length > 1 &&
      simcares['alt'].classNames.length < 9;
</script>

{#if simcares  }
<Container label="">
   <p class="title">Acceptance plot</p>
</Container>
{#if Object.keys(simcares).length > 1 }
<Container label='Results:'>
   <Select id='resType' bind:value={resID} options={Object.keys(simcares)} />
</Container>
{/if}
<Container label='Transform:'>
   <Select id='limType' bind:value={transType} options={['no', 'log']} />
</Container>
<Container label='Labels:'>
   <Select id='showLabels' bind:value={showLabels} options={['hide', 'show']} />
</Container>
{#if colorByValid}
<Container label='Color by:'>
   <Select id='colorby' bind:value={colorBy} options={['roles', 'classes']} />
</Container>
{/if}
{/if}

