<script>
   /*************************************************************
   * AppContainer for target predictions                        *
   * -----------------------------------                        *
   * app container for testing of SIMCA model                   *
   * on target class                                            *
   **************************************************************/

   import { simca_predict } from '../wasm/simca';
   import { downloadResults, downloadModel } from '../utils';

   import ResultTable from './ResultTable.svelte';
   import AppPlots from './AppPlots.svelte';

   import DataLoader from '../shared/PredictDataLoader.svelte';
   import ModelLoader from '../shared/ModelLoader.svelte';
   import ModelTable from '../shared/ModelTable.svelte';
   import SIMCASettings from '../shared/SIMCASettings.svelte';

   import Container from '../../../shared/controls/Container.svelte';
   import Button from '../../../shared/controls/Button.svelte';
   import Outliers from '../../../shared/widgets/Outliers.svelte';
   import ModalContributions from '../modals/Contributions.svelte';


   /*****************************************/
   /* Component parameters                  */
   /*****************************************/

   export let WASM; // WASM object from parent app


   /*****************************************/
   /* Component code                        */
   /*****************************************/

   // main properties of the test app
   let model;                 // PCA model
   let simca;                 // SIMCA parameters
   let data;                  // data set
   let pcares;                // PCA results for the new data set
   let simcares;              // combined results for SIMCA classification
   let ncomp = 0;             // number of components selected by user

   // parameters to check if model or simca object have been updated
   let modelHash = 0;
   let simcaHash = 0;

   // settings for outliers
   let outliers = [];         // vector with dataset row numbers (from 0) marked as outliers
   let samples = [];          // vector with dataset row numbers (from 0) without outliers
   let classes;               // vector with class names for data rows without outliers
   let labels;                // vector with row labels for data rows without outliers
   let varlabels;             // vector with column labels for data columns without removed variables

   // flag and object id container for showing contributions plot
   let isContributionModalShown = false;
   let outlierId = -1;


   /**
    * Show contributions plot for the selected object
    * @param e - event dispatched by AppPlots component with id of the selected object
    */
   function showContributionsModal(e) {
      const id = samples[parseInt(e.detail.id)];
      if (id >= 0 && !outliers.includes(id) && samples.includes(id)) {
         outlierId = id;
         isContributionModalShown = true;
      }
   }


   /**
    * Close contributions plot
    * @param e - event dispatched by ContributionsPlot component
    */
   function closeContributionsModal(e) {
      outlierId = -1;
      isContributionModalShown = false;
   }


   /**
    * Set the selected point as outlier and reset the model and results
    * @param e - event dispatched by AppPlots component with id of the selected point
    */
   function setOutlier(e) {
      const id = samples[parseInt(e.detail.id)];

      if (id >= 0 && !outliers.includes(id) && samples.includes(id)) {

         // find index of the selected point in samples array
         const pos = samples.findIndex(x => x === id);
         if (pos < 0) return;


         // remove id of selected point from classes and labels arrays if they defined
         if (labels) labels = labels.filter((x, i) => i !== pos);
         if (classes) classes = classes.filter((x, i) => i !== pos);

         // remove the selected point from samples array
         samples = samples.filter(x => x !== id);

         // set result objects to null
         simcares = null;
         pcares = null;

         // add id of selected point to outliers array
         outliers = [...outliers, id];
      }
   }


   /**
    * Reset outliers and samples arrays
    */
   function resetOutliers() {

      // reset classes and labels arrays if they defined
      labels = data.labels;
      classes = data.classes;
      varlabels = data.varlabels;

      // reset outliers and samples arrays
      samples = Array(data.values.nrows).fill().map((element, index) => index);
      outliers = [];
   }

   // if model is null - reset dataset
   $: if (model === null) {
      data = null;
      simca = null;
      varlabels = null;
      ncomp = 0;
      modelHash = 0;
      simcaHash = 0;
   }

   // if dataset is null - reset all dependent objects (results, labels, outliers, etc.)
   $: if (data === null) {
      pcares = null;
      simcares = null;
      classes = null;
      labels = null;
      samples = [];
      outliers = [];
   } else if (data && samples.length === 0) {
      samples = Array(data.values.nrows).fill().map((element, index) => index);
      labels = data.labels;
      classes = data.classes;
   }


   // check hashes and initiate updates if outcomes changed
   $: if (model && model.hash !== modelHash) {
      modelHash = model.hash;
      varlabels = model.varlabels;
   }

   $: if (simca && pcares) {
      simcaHash = simca.hash;
      simcares = {
         target: simca_predict(pcares, simca, classes, labels, varlabels)
      };
   }
</script>

{#if isContributionModalShown}
<ModalContributions {data} {simca} {model} {ncomp} selected={outlierId} on:close={closeContributionsModal} />
{/if}

<div class="app-container">

   <!-- left column with main app controls -->
   <div class="app-controls">
      <div class="data-loader">
         <ModelLoader bind:model={model} bind:simca={simca} />
      </div>

      {#if model}
      <div class="data-loader">
         <DataLoader bind:dataset={data} bind:pcares={pcares} {WASM} {model} {outliers} />
      </div>
      {/if}

      {#if outliers.length > 0 }
      <div class="outliers-pane">
         <Outliers dataset={data} on:resetoutliers={resetOutliers} bind:outliers={outliers} />
      </div>
      {/if}

      {#if simca && data}
      <div class="simca-settings">
         <SIMCASettings {model} bind:simca={simca} bind:ncomp={ncomp} />
         <Container label='Download'>
            <div class="buttons">
            <Button on:click={() => downloadResults(simcares.target, data, "target", outliers, ncomp)}>Results</Button>
            <Button on:click={() => downloadModel(model, simca, ncomp)}>Model</Button>
            </div>
         </Container>
      </div>
      <div class="model-stats">
         <ModelTable {simca} {ncomp} />
      </div>
      {/if}
      {#if simcares}
      <div class="result-stats">
         <ResultTable res={simcares.target} {ncomp} />
      </div>
      {/if}

   </div>

   <!-- right column with help text or with plots and plot controls -->
   <div class="app-content">
      {#if !pcares}
         <div class="help-text">
         <p>
            In this tab you can apply previously trained DD-SIMCA model to a dataset consisting of target class members (at least
            the app will assume this, you can of course provide any dataset). You can use it for testing performance
            of the model, or tune its parameters, if necessary.
         </p>
         {#if !model}
            <p><strong>Step 1</strong></p>
            <p>
               Load previously trained DD-SIMCA model from file.
            </p>
         {:else}
            {#if !data}
            <p><strong>Step 2</strong></p>
            <p>
               Load data set from CSV file. The requirements to the file are the same as for the
               training set. The data may contain class labels for each row (you need to select this option in settings after selecting CSV file) but it is not necessary, as in any case the labels will be ignored and model will assume that all samples are from the target class.
            </p>
            {/if}
         {/if}
         </div>
      {:else}
         {#key data || outliers}
         <AppPlots on:setoutlier={setOutlier} on:plotoutlier={showContributionsModal} {simcares} {ncomp} />
         {/key}
      {/if}
   </div>
</div>

